.formRegister {
  margin: 20px;
}

.form {
  // display: flex;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  flex-direction: column;
  padding: 12px;
  width: 280px;
  border: 1px solid rgb(105, 105, 105);
}

.formLabel {
  span {
    display: block;
    margin-bottom: 10px;
  }
}

.formInput {
  margin-bottom: 15px;
  width: 272px;
  height: 24px;
}

.btn {
  cursor: pointer;
  border-radius: 4px;
  padding: 8px;
  width: 280px;
  border: 1px solid transparent;
  font-size: 16px;
  font-weight: 700;
  color: white;
  background-color: #71928a;
}
